/** @jsx React.DOM */

var React = require('react');

var ShowHideTooltips = React.createClass({displayName: 'ShowHideTooltips',
  render: function() {
    return (
      React.DOM.p(null, 
        'Tooltips: ', 
        React.DOM.a({href: "#", onClick: this.handleShow}, "Show all"), 
        React.DOM.span(null, " - "), 
        React.DOM.a({href: "#", onClick: this.handleHide}, "Hide all")
      )
    );
  },

  handleShow: function(e) {
    e.preventDefault();
    this.props.setAppState({
      showingAllTooltips: true,
      // Disable animation
      prevDomain: null
    });
  },

  handleHide: function(e) {
    e.preventDefault();
    this.props.setAppState({
      showingAllTooltips: false,
      prevDomain: null
    });
  }
});

module.exports = ShowHideTooltips;



